___CSS_LOADER_EXPORT___.push([module.id, `body,
p,
h1,
img,
nav,
li,
ul,
h2,
div {
  margin: 0;
  padding: 0;
}

.carousel-element {
  position: absolute;
  top: calc(50% - 70px);
  left: calc(50% - 70px);
  width: 142px;
  overflow: hidden;
  display: grid;
}

.slides {
  display: grid;
  grid-auto-flow: column;
}

img {
  margin: 20px;
}

.picture-frame {
  width: 140px;
  overflow: hidden;
  border: 1px solid;
  margin-bottom: 10px;
}

.navigation-dots {
  display: grid;
  grid-auto-flow: row;
  justify-content: center;
  justify-items: center;
  overflow: auto;
  grid-template-columns: repeat(auto-fill, minmax(10px, 1fr));
  gap: 5px;
  max-width: 142px;
  margin-bottom: 20px;
}

.navigation-dot {
  width: 5px;
  height: 5px;
  background-color: white;
  border: 1px solid;
  border-radius: 5px;
  cursor: pointer;
}

.navigation-dot[active='true'] {
  background-color: black;
}

.buttons {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;;;;;;;;;EASE,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,kBAAkB;EAClB,qBAAqB;EACrB,sBAAsB;EACtB,YAAY;EACZ,gBAAgB;EAChB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,qBAAqB;EACrB,cAAc;EACd,2DAA2D;EAC3D,QAAQ;EACR,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,UAAU;EACV,WAAW;EACX,uBAAuB;EACvB,iBAAiB;EACjB,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;AACzB","sourcesContent":["body,\np,\nh1,\nimg,\nnav,\nli,\nul,\nh2,\ndiv {\n  margin: 0;\n  padding: 0;\n}\n\n.carousel-element {\n  position: absolute;\n  top: calc(50% - 70px);\n  left: calc(50% - 70px);\n  width: 142px;\n  overflow: hidden;\n  display: grid;\n}\n\n.slides {\n  display: grid;\n  grid-auto-flow: column;\n}\n\nimg {\n  margin: 20px;\n}\n\n.picture-frame {\n  width: 140px;\n  overflow: hidden;\n  border: 1px solid;\n  margin-bottom: 10px;\n}\n\n.navigation-dots {\n  display: grid;\n  grid-auto-flow: row;\n  justify-content: center;\n  justify-items: center;\n  overflow: auto;\n  grid-template-columns: repeat(auto-fill, minmax(10px, 1fr));\n  gap: 5px;\n  max-width: 142px;\n  margin-bottom: 20px;\n}\n\n.navigation-dot {\n  width: 5px;\n  height: 5px;\n  background-color: white;\n  border: 1px solid;\n  border-radius: 5px;\n  cursor: pointer;\n}\n\n.navigation-dot[active='true'] {\n  background-color: black;\n}\n\n.buttons {\n  display: grid;\n  grid-auto-flow: column;\n  justify-content: center;\n}"],"sourceRoot":""}]);
